.single-product {
	&:nth-child(n + 3) {
		margin-top: 60px;
	}
}
.product-price {
	font-size: 30px;
	opacity: 0.7;
	margin-bottom: 30px;
}
.product-image {
	margin-bottom: 36px;
	height: 515px;
	img {
		width: 100%;
		height: 100%;
		object-fit: cover;
	}
}
.head-content {
	width: 76%;
}
